
<template>
  <swiper :navigation="true" :slidesPerView="'auto'" @click="onModalOpen">
    <swiper-slide v-for="image in images" :key="image.id" >
        <img :src="image.imageUrl"/>
    </swiper-slide>
    <swiper-slide></swiper-slide>
  </swiper>
  <teleport to="body">
      <SwiperOverlay :slides="images" :indexToShowFrom="indexToShowFrom" v-if="modalOpen" @closePopup="modalOpen=false"/>
  </teleport>
</template>
<script>
import { Swiper, SwiperSlide } from 'swiper/vue';
import SwiperOverlay from '../components/swiperOverlay.vue';

import SwiperCore, {
  Navigation
} from 'swiper/core';

SwiperCore.use([Navigation]);


export default {
  components: {
    Swiper,
    SwiperSlide,
    SwiperOverlay
  },
  data() {
    return {
      isOpacity: false,
      modalOpen:false,
      indexToShowFrom:undefined,
      images: [
        { id: 1, imageUrl: require("../../assets/images/TeamG/certificate/certificate1.png").default },
        { id: 2, imageUrl: require("../../assets/images/TeamG/certificate/certificate2.png").default },
        { id: 3, imageUrl: require("../../assets/images/TeamG/certificate/certificate3.png").default },
        { id: 4, imageUrl: require("../../assets/images/TeamG/certificate/certificate1.png").default },
        { id: 5, imageUrl: require("../../assets/images/TeamG/certificate/certificate2.png").default },
        { id: 6, imageUrl: require("../../assets/images/TeamG/certificate/certificate3.png").default },
        { id: 7, imageUrl: require("../../assets/images/TeamG/certificate/certificate1.png").default },
        { id: 8, imageUrl: require("../../assets/images/TeamG/certificate/certificate2.png").default },
        { id: 9, imageUrl: require("../../assets/images/TeamG/certificate/certificate3.png").default },
        { id: 10, imageUrl: require("../../assets/images/TeamG/certificate/certificate3.png").default },
        { id: 11, imageUrl: require("../../assets/images/TeamG/certificate/certificate3.png").default },
      ],
    };
  },
  methods: {
      checkClick() {
          console.log('click sertificate');
      },
      onModalOpen(e){
      if (e.clickedIndex !== undefined){
        this.modalOpen=true;
        this.indexToShowFrom=e.clickedIndex;
      }
    }
  },

}
</script>


<style lang="scss" scoped>
@import 'swiper/swiper.min.css';
@import 'swiper/components/navigation/navigation.min.css';

.swiper-container {
    width: 100%;
    max-height: 380px;
    height: 100%;
    border-top: solid 2px #F3EFEC;
    border-bottom: solid 2px #F3EFEC;
    ::v-deep(.swiper-button-prev) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        left: 62px !important;
    }
    ::v-deep(.swiper-button-next) {
        --swiper-navigation-size: 20px;
        --swiper-navigation-color: #000;
        opacity: 1;
        right: 58px !important;
    }
    ::v-deep(.swiper-wrapper) {
        left: 250px;
    }
}



.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: solid 2px #F3EFEC;
    border-right: solid 2px #F3EFEC;
    padding: 0 39px 0 39px;
    max-width: 18.7vw !important;
    height: auto;
    &::after {
        content: url('../../assets/images/zoom-img.svg');
        display: none;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.1);
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }
    &:hover::after {
        display: flex;
    }
  }

.swiper-slide-prev {
    pointer-events: none;
    &::after {
        content: '';
        position: unset;
        display: none;
        background-color: unset;
        width: unset;
        height: unset;
    }
    & img {
        opacity: 0.5;
    }
}

.swiper-slide-next + .swiper-slide + .swiper-slide {
    pointer-events: none;
    &::after {
        content: '';
        position: unset;
        background-color: unset;
        width: unset;
        height: unset;
        display: none;
    }
    & img{
        opacity: 0.5;
    }
  }

// .isOpacity {
//    opacity: 0.5;
// }

.swiper-slide img {
    display: block;
    max-width: 18.7vw !important;
    height: auto;
    object-fit: cover;
  }

@media screen and (max-width: 1023px) and (min-width: 768px) {
    .swiper-container {
        ::v-deep(.swiper-button-prev) {
            display: none;
        }
        ::v-deep(.swiper-button-next) {
            display: none;
        }
        ::v-deep(.swiper-wrapper) {
            left: 100px;
        }
    }
  }

@media screen and (max-width: 767px) {
    .swiper-container {
        ::v-deep(.swiper-button-prev) {
            display: none;
        }
        ::v-deep(.swiper-button-next) {
            display: none;
        }
        ::v-deep(.swiper-wrapper) {
            left: 100px;
        }
    }
    .swiper-slide-next + .swiper-slide {
        pointer-events: none;
        &::after {
            content: '';
            position: unset;
            background-color: unset;
            width: unset;
            height: unset;
            display: none;
        }
        & img{
            opacity: 0.5;
        }
    }
  }




</style>




